<template>
    <div class="box">
        <h3 class="top-header content-position back-color">百度音乐歌单搜索</h3>
        <div class="content-position back-color raw" style="line-height: 3">
            <input type="text" class="back-color col-8 content-search" v-model="actor"/>
            <button class="col-4 content-shadow search-btn back-color" @click="get">搜索</button>
        </div>
        <p v-if="!flag" style="color:#fff">数据正在加载中。。。</p>
        <div class="content-position row content-music" style="position: relative" v-for="item in music" v-if="flag">
            <div class="content-bg"></div>
            <div class="col-4" style="z-index: 3">
                <div class="content-image">
                    <img :src="item.pic_small" style="border-radius: 50%;width:60px;height: 60px;" />
                </div>
            </div>
            <div class="col-8 content-data">
                <span>歌曲:{{item.song_name}}</span><br />
                <span>歌手:{{item.artist_name}}</span><br />
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "baidu",
        data:function () {
            return {
                flag:false,
                actor:"陈奕迅",
                music:{},
            }
        },
        created:function () {
            this.$http.jsonp("https://api.shenjian.io/?appid=fedc7ec3472574b7f2bb7df5920e9075&keyword=%E9%99%88%E5%A5%95%E8%BF%85&pageNo=1&pageSize=20").then(function (result) {
                this.music=result.body.data;
                this.flag=true;
            })
        },
        methods:{
            get:function () {
                this.flag=false;
                this.$http.jsonp(this.link).then(function (result) {
                    this.music=result.body.data;
                    this.flag=true;
                })
            }
        },
        computed:{
            link:function () {
               var a="https://api.shenjian.io/?appid=fedc7ec3472574b7f2bb7df5920e9075&keyword="+this.actor+"&pageNo=1&pageSize=20" ;
               return a;
            }
        }
    }
</script>

<style scoped>
    .box{
        margin:0;
        background-color:#1d1d1d ;
        padding:20px 0;
        padding-bottom: 100px;
    }
    .top-header{
        line-height: 5;
        font-size: 16px;
        color:#fff;
        letter-spacing: 0.5em;
    }
    .back-color{
        background-color:#3d3d3d ;
    }
    .content-position{
        width:90%;
        margin:0px auto;
        border-radius: 20px;
        box-shadow: 0 0 40px rgba(0, 0, 0, 0.8);
        margin-bottom: 20px;
    }
    .content-search{
        color:#fff;
        outline: none;
        padding-left:2em;
        border:none;
        box-shadow: 0 0 40px rgba(0, 0, 0, 0.8);
        border-radius: 20px 0 0 20px;
    }
    .content-shadow{
        box-shadow: 0 0 40px rgba(0, 0, 0, 0.8);
    }
    .search-btn{
        color:#fff;
        border-radius: 0 20px 20px 0;
        outline: none;
        border:none;
    }
    .content-music{
        padding: 20px 10px;

    }
    .content-bg{
        width: 100%;
        height:100%;
        position: absolute;
        top:0;
        left:0;
        z-index: 2;
        background-image: url("../assets/bg.jpg");
        background-repeat: no-repeat;
        background-position: center;
        background-size: 100%;
        filter: blur(20px);
    }
    .content-image{
        padding:20px;
        width:80px;
        height:80px;
        background-color:#202020;
        border-radius: 50%;
        padding: 10px
    }
    .content-data{
        color:white;
        line-height: 40px;
        z-index: 3;
    }
</style>